	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"King Kurt To Step Down","date":"16 Jan 2002","slug":"/2002-01-16-king-kurt-to-step-down/","tags":"Y2002"},"html":"<p>Not much interesting news today... just that Kurt Biedenkopf announced that he will <a href=\"http://www.expatica.com/germany.asp?pad=190,205,&amp;item_id=18517\">resign as Prime Minister in Saxony in April</a>. He's been irrelevent for so long,  it wouldn't be worth noting except that it's a slow news day. It's ironic, though, that like his former rival Helmut Kohl, he stayed in office one term too long.</p>"},"next":{"fields":{"title":"A Post From Radio to MT","date":"16 Jan 2002","slug":"/2002-01-16-a-post-from-radio-to-mt/"}},"previous":{"fields":{"title":"OK, a post to Mt","date":"16 Jan 2002","slug":"/2002-01-16-ok-a-post-to-mt/"}}},{"node":{"fields":{"title":"A Post From Radio to MT","date":"16 Jan 2002","slug":"/2002-01-16-a-post-from-radio-to-mt/","tags":"Y2002"},"html":"<p>Yes, folks, it can be done. Just remember, the blog_id for MT is an integer!</p>"},"next":{"fields":{"title":"Still Running","date":"15 Jan 2002","slug":"/2002-01-15-still-running/"}},"previous":{"fields":{"title":"King Kurt To Step Down","date":"16 Jan 2002","slug":"/2002-01-16-king-kurt-to-step-down/"}}},{"node":{"fields":{"title":"Still Running","date":"15 Jan 2002","slug":"/2002-01-15-still-running/","tags":"Y2002"},"html":"<p>My <a href=\"http://radio.weblogs.com/0100464/2002/01/15.html\">Radio is still on</a>, if anyone is interested.</p>\n<p>We've now got a time and a place in Las Vegas: Sunday 21 April though Friday 26 April, Hotel <a href=\"http://www.parislasvegas.com\">Paris Las Vegas</a>. The hotel was chosen for us. <i>And yes, we'd love to meet up with <a href=\"http://hal.editthispage.com/\">Hal and family!</a></i></p>\n<p>Christopher and I have had our last Monday alone together for a while. I simply cannot put in a full day's work at home alone with an active two-year-old. We'll be sending him to day care for at least part of the day starting next week.</p>\n<p>German bureaucracy moves slowly, which can be advantageous at times. We were deliberately slow sending in our 2000 tax return to the local Finanzamt, knowing we'd owe a medium size chunk. We finally got the statement just before we left on vacation in December, as well as a request for some additional paperwork. We \"forgot\", and today got a polite reminder. But they still haven't booked anything from our bank account.</p>"},"next":{"fields":{"title":"Islamic Animal Slaughter To Be Allowed","date":"15 Jan 2002","slug":"/2002-01-15-islamic-animal-slaughter-to-be-allowed/"}},"previous":{"fields":{"title":"A Post From Radio to MT","date":"16 Jan 2002","slug":"/2002-01-16-a-post-from-radio-to-mt/"}}}],"pathPrefix":"","first":false,"last":false,"index":945,"pageCount":1101}}

